package flowcomponents

import kotlinx.browser.document
import kotlinx.html.*
import kotlinx.html.js.header
import kotlinx.html.js.onChangeFunction
import org.w3c.dom.HTMLElement


inline fun TagConsumer<HTMLElement>.myHeader() {
    header("header") {
        a(classes = "titleLogo") {
            href = "#"
            +"Alex's Place"
        }
        input(classes = "hamMenuCkb") {
            type = InputType.checkBox
            id = "hamMenuCkb"
        }
        label("hamMenuIconLbl") {
            htmlFor = "hamMenuCkb"
            span("navicon") {
            }
        }
        ul("menu") {
            li {

                a(href = "#") { +"About Me" }
            }
            li {
                a(href = "#") { +"Resume" }
            }
            li {
                a(href = "#") { +"Projects" }
            }
            li {
                a(href = "#") { +"Misc" }
            }
        }



        darkModeSwitch()



        style {

            +"""

                @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
                @import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');


.dark .header {
      background-color: rgba(66,66,75,0.3);
      box-shadow:
         1px 1px 5px 2px rgba(11,5,15,0.7),
         3px 4px 5px 10px rgba(11,5,15,0.15);
      border: 1px solid rgba(133,133,133,0.15);
}
.dark .header ul {
    background-color: rgba(70,70,85,0.15)
}

                .header {
                margin-left: 5px;
                    padding-top: 4px;
                    padding-bottom: 4px;
                  background-color: #eaeaea;
                  box-shadow:
                     -1px 2px 2px 3px rgba(51,53,51,0.25),
                     -2px 4px 3px 4px rgba(51,53,51,0.09),
                     -3px 5px 4px 7px rgba(77,77,77,0.05);
                  
                  position: fixed;
                  width: 100%;
                  z-index: 3;
                }


                .header ul {
                  background-color: #eaeaea;
                  margin: 0;
                  padding: 0;
                  list-style: none;
                  overflow: hidden;
                  
                }



                .header li a {
                    color: #140521;
                    display: block;
                    padding: 20px 20px;
                    border-right: 1.7px solid #f4f4f4;
                    text-decoration: none;
                    text-shadow: 0 0 1px #140521;
                    font-size: 125%;
                }




.dark .header li a:hover {
                    background-color: rgba(11,10,10, 0.05);
                                        box-shadow:
                        inset 2px 2px 1px 2px rgba(8,8,10,0.27),
                        inset 2px 2px 2px 4px rgba(0,0,0,0.12),
                        inset 3px 3px 3px 9px rgba(0,0,0,0.08);
}       

                .header li a:hover,
                .header .hamMenuCkb:hover {
                    background-color: #f2eff2;
                    box-shadow:
                        inset 2px 1px 1px 2px rgba(100,101,109,0.63),
                        inset 3px 2px 2px 5px rgba(99,99,99,0.16),
                        inset 4px 3px 4px 12px rgba(66,66,66,0.07);
                            
                    text-shadow: 1px 1px 2px #140521;
                    transform: translate(1px, 1px);

	
                    
                }
                
                
                
                .dark .header li a::before {
                    background-color: lightgray;
                    
                }
                .header li a::before {
                  content: '';
                  position: absolute;
                  width: 70%;
                  height: 0.22em;
                  border-radius: 6px;
                  background-color: rgba(1,1,1,0.7);
                  bottom: 20%;
                  left: 2.5%;
                  right: 57%;
                  transform-origin: right;
                  transform: scaleX(0);
                  transition: transform .85s ease-out;
                }
                .header li a:hover::before {
                  transform-origin: left;
                  transform: scaleX(1);
                }
                
      
                
                
.header .titleLogo:visited {
color: unset;
}
                .header .titleLogo {
                    font-family: 'Rubik', sans-serif;
                    display: block;
                    float: right;
                    font-size: 2.25em;
                    padding: 10px 20px;
                    text-decoration: none;
                    margin-right: 0.9vw;
                    float:left;
                    border-right: 0.12em solid gray;
                    border-radius: 0.01em;
                    
                }

                /* menu */

                .header .menu {
                  font-family: 'Lato', sans-serif;
                  clear: both;
                  max-height: 0;
                  transition: max-height .4s ease-out;
                }

                /* menu icon */

                .header .hamMenuIconLbl {
                  cursor: pointer;
                  display: inline-block;
                  float: left;
                  padding: 26px 16px;
                  padding-top: 28px;
                  position: relative;
                  user-select: none;
                }

.dark .header .hamMenuIconLbl .navicon,
.dark .header .hamMenuIconLbl .navicon:before,
.dark .header .hamMenuIconLbl .navicon:after {
    background: #ddd;
}

                .header .hamMenuIconLbl .navicon {
                  background: #222;
                  display: block;
                  height: 4px;
                  position: relative;
                  transition: background .4s ease-out;
                  width: 44px;
                }

                .header .hamMenuIconLbl .navicon:before,
                .header .hamMenuIconLbl .navicon:after {
                  background: #333;
                  content: '';
                  display: block;
                  height: 100%;
                  position: absolute;
                  transition: all .44s ease-out;
                  width: 100%;
                }

                .header .hamMenuIconLbl .navicon:before {
                  top: 2vh;
                }
                .header .hamMenuIconLbl .navicon:after {
                  top: -2vh;
                }



                /* menu btn */
                .header .hamMenuCkb {
                  display: none;
                }

                .header .hamMenuCkb:checked ~ .menu {
                  max-height: 240px;
                }

                .header .hamMenuCkb:checked ~ .hamMenuIconLbl .navicon {transition: transform .33s ease-in-out;
                  background: transparent;
                }

                .header .hamMenuCkb:checked ~ .hamMenuIconLbl .navicon:before {
                  transform: rotate(-45deg);
                }

                .header .hamMenuCkb:checked ~ .hamMenuIconLbl .navicon:after {
                  transform: rotate(45deg);
                }

                .header .hamMenuCkb:checked ~ .hamMenuIconLbl:not(.steps) .navicon:before,
                .header .hamMenuCkb:checked ~ .hamMenuIconLbl:not(.steps) .navicon:after {
                  top: 0;
                }

                /* 48em = 768px */

                .header .menu li {
                    position: relative;
                 }


                @media (min-width: 48em) {
                .header li a::before {
                    left: 19%;
                    right: 21%;
                    transition: transform .4s ease-out;
                }
                  .header li {
                    float: left;
                    
                  }
                  .header li a {
                    padding: 20px 30px;
                  }
                  .header .menu {
                    clear: none;
                    float: left;
                    max-height: none;
                  }
                  .header .hamMenuIconLbl {
                    display: none;
                  }
                  
                  
                  
                 
              
                  
                }

                
            """.trimIndent()

        }

    }
}

inline fun FlowContent.darkModeSwitch() {
    div("ckbDarkMode") {
        style = """
                width: 60px;
                height: 18px;
                background: gray;
                border: 1px solid darkgray;
                position: absolute;
                right: 10px;
                top: 2px;
                border-radius: 6px;
                float: right;
                margin-top: 1.55em;
                margin-right: 5vw;
                transition: background-color 500ms;
                
            """.trimIndent()
        checkBoxInput {
            id = "inpCkbDarkMode"
            type = InputType.checkBox
            style = "visibility: hidden;"
            onChangeFunction = { ev ->
                val targ = ev.target

                if (targ.asDynamic().checked is Boolean) {
                    val checkStatus = targ.asDynamic().checked as Boolean
                    if (checkStatus) {
                        document.body!!.classList.add("dark")
                        targ.asDynamic().nextElementSibling.style.left = "37px"
                        targ.asDynamic().parentElement.style.backgroundColor = "#cccccc"


                    } else {
                        document.body!!.classList.remove("dark")
                        targ.asDynamic().nextElementSibling.style.left = "-7px"
                        targ.asDynamic().parentElement.style.backgroundColor = "gray"
                    }

                }
                //document.body!!.classList.add("dark")

            }

        }
        label("darkmodelabel") {

            style = """
                   display: block;
                   
                    width:  29px;
                    height: 29px;
                    border-radius: 50%;
                    transition:
                        all .5s ease,
                        border .1s; 
                    cursor: pointer; 
                    position: absolute;
                    top: -6px;
                    left: -8px;
                    background: #333333;
            """.trimIndent()
            htmlFor = "inpCkbDarkMode"

        }
        style {
            +"""
      
      
      
      
      
html,
body {
  transition: background-color 500ms;
  
}

.dark .darkmodelabel {
    border: 2px solid white;
}

.dark {
  background: #333333;
}


.ckbDarkMode::before {
    content: 'dark mode:';
    position: absolute;
    top: -18px;
    left: -2px;
    font-size: 74%;
    font-weight: bold;
    font-family: monospace;
    white-space: nowrap;
    opacity: 85%;
    color: darkgray;
    
    
}

.dark header,
.dark header ul,
.dark header ul a {
    background-color: rgba(50,50,50,0.2);
    color: #cccccc;
}

.dark .header li a {
    border-right: 1.7px solid black;
}
.header input[type=checkbox]{
  visibility: hidden;
}




  



      
                """
        }
    }
}

